import React, { Component } from 'react'
import { Layout, Menu, Calendar } from 'antd'
import ArticleList from '../../Compoents/ArticleList'
import LookArticle from '../../Compoents/LookArticle'
import Cookies from 'js-cookie'

const { Header, Content, Footer } = Layout
var logo =  require('../../Assets/mylogo.png')
class Index extends Component {
    constructor(props) {
        super()
        this.state = {
            isLogin: false,
            params: {},
            lookArticle: false
        }
    }
    goToLogin = () => {
        this.props.history.push("/login");
    }
    goToPublish = () => {
        this.props.history.push("/publish");
    }
    goToIndex = () => {
        this.props.history.push("/");        
    }
    componentWillMount() {
       let userId =  Cookies.get('user_id')
       if (userId) {
           this.setState({
               isLogin: true
           })
       }
       let params = this.props.match.params
       if (params.id) {
        this.setState({
            params,
            lookArticle: true
        })
       }
       console.debug('输出id', params.id)
    }
    render() {
        return (
            <Layout>
                <Header>
                    <div className="logo" style={{marginTop: -5}} onClick={this.goToIndex}>
                        <img alt="闻风" src={logo}/>
                    </div>
                    <Menu
                        theme = "dark"
                        mode = "horizontal"
                        defaultSelectedKeys = {['1']}
                        style={{lineHeight: '64px', float: 'right'}}
                    >
                        <Menu.Item key="1">推荐博文</Menu.Item>
                        <Menu.Item key="2">留言信息</Menu.Item>
                        {!this.state.isLogin && <Menu.Item key="3" onClick={this.goToLogin}>登陆</Menu.Item>}
                        {this.state.isLogin && <Menu.Item key="4" onClick={this.goToPublish}>发布文章</Menu.Item>}
                    </Menu>
                </Header>
                <Layout>
                <Content>
                    <div style={{minHeight: 580, background: '#fff', float: 'left', margin: 24, padding:24, boxSizing: 'border-box', width: '70%'}}>
                        { !this.state.lookArticle && <ArticleList history={this.props.history} lookArticle={this.state.lookArticle} /> }
                        { this.state.lookArticle && <LookArticle lookArticle={this.state.lookArticle} id={this.state.params.id} /> }
                    </div>
                    <div style={{width:'25%', float: "right", minHeight: 580, background: '#fff', marginTop: '24px', paddingTop: 30}}>
                        <Calendar fullscreen={false}></Calendar>
                    </div>
                </Content>
                <Footer style={{textAlign: 'center'}}> Bolg ©2019 Created by 澜庭有雨</Footer>
                </Layout>
            </Layout>
        )
    }
}
export default Index