import React, { Component } from 'react'
import ReactMarkdown from 'react-markdown'
import commonApi from 'axios'
import moment from 'moment'
class Article extends Component {
    constructor(props) {
        super()
        this.state = {
            article: [],
            username: ''
        }
    }
    componentDidMount() {
        console.debug(this.props)
        if (!this.props.lookArticle) {
            return
        }
        commonApi({
            url: '/article/get',
            method: 'POST',
            data: {
                id: this.props.id
            }
        }).then(res => {
            console.debug('获取的文章', res)
            if (res.data) {
                res.data.createTime = parseInt(res.data.createTime)
                this.setState({
                    article: res.data,
                    username: res.data.user.username
                })
            }
        })
    }
    render() {
        const art = this.state.article
        return (
            <div>
                <div className='article-title'><h1 style={{marginBottom: 6}}>{art.title}</h1></div>
                <div className="userInfo"><span style={{marginRight:15}}>{'作者：' + this.state.username}&nbsp;</span>{'发表时间：' + moment(new Date(art.createTime)).format('YYYY-MM-DD HH:mm:ss')}</div>
                <ReactMarkdown className="article" source={art.content} />
            </div>
        )
    }
}
export default Article