import React, { Component } from 'react'
import { List, Avatar, Icon } from 'antd'
import commonApi from 'axios'

const IconText = ({type, text}) => (
    <span>
        <Icon type={type} style={{ marginRight: 8 }}/>
        {text}
    </span>
)

class ArticleList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            listData: []
        }
    }
    goToArticle(id) {
        this.props.history.push('/article/' + id);
    }
    componentDidMount() {
        console.debug(this.props.lookArticle)
        if (this.props.lookArticle) {
            return
        }
        commonApi({
            url: '/article/list',
            method: 'GET'
        }).then(res  => {
            console.debug(res)
            res.data.forEach(item => {
                item.avatar = item.user.avatar
            })
            this.setState({
                listData: res.data
            })
        })
    }
    render() {
        return (
            <List
                itemLayout="vertical"
                size="large"
                dataSource={this.state.listData}
                renderItem={(item) => (
                    <List.Item
                        key={item.title}
                        actions={[<IconText type="star-o" text={item.collect} />, <IconText type="like-o" text={item.like} />, <IconText type="message" text={item.comment} />]}
                        extra={<img width={272} alt="logo" src={item.img} />}
                    >
                        <List.Item.Meta 
                            title={<a onClick={() => {this.goToArticle(item._id)}}>{item.title}</a>}
                            avatar={<Avatar src={item.avatar}></Avatar>}
                         />
                        {item.desc}
                    </List.Item>  
                )}
            />
        )
    }
}
export default ArticleList