import React, { Component } from 'react'
import commonApi from 'axios'
import { Form, Input, Icon, Button, message, Tabs, Select } from 'antd'
import Cookies from 'js-cookie'

const { Option } = Select 
class Login extends Component {
    constructor(props) {
        super()
    }
    componentWillMount() {
        if (Cookies.get('user_id')) {
            this.props.history.push("/");
        }
    }
    // 登陆提交
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields(['userName', 'password'], (err, values) => {
        if (!err) {
            console.log('Received values of form: ', values);
            commonApi({
                url: '/users/login',
                method: 'POST',
                data: {
                    loginname: values.userName,
                    password: values.password
                }
            }).then(res => {
                console.debug('登陆成功', res)
                message.success("登陆成功");
                this.props.history.push("/");
            }).catch(err => {
                message.error(err.response.data.message);
                console.debug('登录失败', err.response.data.message)
            })
        }
        console.debug('开始提交了')
        });
    }
    // 注册提交
    registerFormSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields(['username', 'regPassword', 'loginname', 'confirmPassword', 'email', 'sex'], (err, values) => {
        if (!err) {
            console.log('Received values of form: ', values);
            if (values.regPassword  !== values.confirmPassword) {
                message.error('两次密码输入不一致')
                return
            }
            commonApi({
                url: '/users/reg',
                method: 'POST',
                data: {
                    username: values.username,
                    password: values.regPassword,
                    sex: values.sex,
                    loginname: values.loginname,
                    email: values.email
                }
            }).then(res => {
                message.success('注册成功')
            }).catch(err => {
                console.debug(err)
                message.error('注册失败')
            })
        }
        console.debug('开始提交了')
        });
    }
    render() {
        // 获取字段装饰器
        const { getFieldDecorator } =  this.props.form
        return (
            <div className='loginForm'>

        <Tabs defaultActiveKey="1">
            <Tabs.TabPane tab="登陆" key="1">
            <Form onSubmit={this.handleSubmit}>
                <Form.Item>
                    {
                        getFieldDecorator('userName', {
                            rules: [{required: true, message: '请输入用户名'}]
                        })(
                        <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)'}} /> } placeholder="请输入登陆名" />
                        )
                    }
                </Form.Item>
                <Form.Item>
                    {
                        getFieldDecorator('password', {
                            rules: [{required: true, message: '请输入密码'}]
                        })(
                            <Input prefix={<Icon type='lock' style={{color: 'rgba(0, 0, 0, .25)'}}></Icon>} type="password" placeholder="请输入密码"></Input>
                        )
                    }
                </Form.Item>
                <Form.Item>
                    <Button
                        type="primary"
                        htmlType="submit"
                    >登陆</Button>
                </Form.Item>
            </Form>
            </Tabs.TabPane>
            <Tabs.TabPane tab="注册" key="2">
              <Form onSubmit={this.registerFormSubmit}>
                <Form.Item>
                    {getFieldDecorator('loginname', { rules: [{ required: true, message: '请输入登陆名' }] })(
                        <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)'}} /> } placeholder="请输入登陆名"></Input>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('username', { rules: [{ required: true, message: '请输入用户名' }] })(
                        <Input prefix={<Icon type="smile" style={{ color: 'rgba(0,0,0,.25)'}} /> } placeholder="请输入用户名"></Input>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('email', { rules: [{ type: 'email', required: true, message: '请输入邮箱' }] })(
                        <Input prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)'}} /> } placeholder="请输入邮箱地址"></Input>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('regPassword', { rules: [{ required: true, message: '请输入密码' }, { min: 5, message: '最低为6个字符' }] })(
                        <Input prefix={<Icon type='lock' style={{color: 'rgba(0, 0, 0, .25)'}}></Icon>} placeholder="请输入密码" type="password"></Input>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('confirmPassword', { rules: [{ required: true, message: '请再次输入密码' }] })(
                        <Input prefix={<Icon type='lock' style={{color: 'rgba(0, 0, 0, .25)'}}></Icon>} placeholder="请再次输入密码" type="password"></Input>
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('sex', { rules: [{ required: true, message: '请选择性别' }] })(
                        <Select placeholder="选择性别">
                            <Option value="1">男</Option>
                            <Option value="0">女</Option>
                        </Select>
                    )}
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit">
                        注册
                    </Button>
                </Form.Item>
              </Form>
            </Tabs.TabPane>
        </Tabs>
        </div>

        )
    }
}
const Logins = Form.create()(Login);
export default Logins