import React, { Component } from 'react'
import commonApi from 'axios'
import { Form, Input, Button, message, Upload, Icon} from 'antd'
import Cookies from 'js-cookie'
import Editor from 'for-editor'

class Login extends Component {
    constructor(props) {
        super()
        this.state = {
            fileList: [],
            content: ''
        }
    }
    componentWillMount() {
        if (!Cookies.get('user_id')) {
            this.props.history.push("/");
        }
    }
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
        if (!err) {
            console.log('Received values of form: ', values);
            if (this.state.fileList.length === 0) {
                message.error('请上传封面图片')
                return
            }
            const formData = new FormData();
            formData.append('logo', this.state.fileList[0])
            commonApi({
                url: '/file/upload',
                method: 'POST',
                data: formData
            }).then(img => {
                commonApi({
                    url: '/article/publish',
                    method: 'POST',
                    data: {
                        title: values.title,
                        content: this.state.content,
                        img: img.data.filePath
                    }
                }).then(res => {
                    console.debug('发布成功', res)
                    message.success("发布成功");
                    this.props.history.push("/");
                }).catch(err => {
                    message.error(err.response.data.message);
                    console.debug('发布失败', err.response.data.message)
                })
            })
            
        }
        console.debug('开始提交了')
        });
    }
    contentChange = (e) => {
        this.setState({
            content: e
        })
    }
    render() {
        const { getFieldDecorator } =  this.props.form
        const props = {
            accept: '.png,.jpg,.jpeg',
            onRemove: (file) => {
                const index = this.state.fileList.indexOf(file)
                const fileList = this.state.fileList.slice()
                fileList.splice(index, 1)
                this.setState({
                    fileList, 
                })
            },
            beforeUpload: (file) => {
                console.debug('输出文件类型', file)
                if (file.type.indexOf('image') < 0) {
                    message.error('请上传图片文件')
                    return false                    
                }
                this.setState({
                    fileList: [file]
                })
                return false
            },
            fileList: this.state.fileList
        }
        return (
        <div className='publishForm'>
            <Form onSubmit={this.handleSubmit}>
                <Form.Item>
                    {
                        getFieldDecorator('title', {
                            rules: [{required: true, message: '请输入文章标题'}]
                        })(
                        <Input  placeholder="请输入文章标题" />
                        )
                    }
                </Form.Item>
                <Form.Item>
                    {
                        getFieldDecorator('content', {
                            rules: [{required: true, message: '请输入文章内容'}]
                        })(
                            // <TextArea placeholder="请输请输入文章内容"></TextArea>
                            <Editor onChange={this.contentChange}/>
                        )
                    }
                </Form.Item>
                <Form.Item label="封面图片">
                    <Upload {...props}>
                        <Button>
                            <Icon type="upload" /> Upload
                        </Button>
                    </Upload>
                </Form.Item>
                <Form.Item>
                    <Button
                        type="primary"
                        htmlType="submit"
                    >发表</Button>
                </Form.Item>
            </Form>
        </div>
        )
    }
}
const Logins = Form.create()(Login);
export default Logins